.palette-display-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
}

.palette-display-card{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 300px;
    border: 1px solid gray;
    border-radius: 5px;
    padding: 20px;
}

.palette-data{
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-right: 30px;
}

.btn-back{
    margin-top: 30px;
    width: 200px;
    text-decoration: none;
    border: 1px solid gray;
    padding: 10px;
    border-radius: 5px;
    color: #000;
}

.btn-back:hover {
    cursor: pointer;
    background-color: rgb(37, 37, 37);
    color: #fff;
}