.tag {
    border-radius: 20px;
    padding: 5px;
    margin-bottom: 10px;
    background-color: #fff;
    border: 1px solid #e9e9e9;
    width: 120px;
  }
  
  .tag:hover {
    cursor: pointer;
    border: 1px solid #e9e9e9;
  }
  
  .active {
    background-color: rgb(37, 37, 37);
    color: #fff;
  }